import React from 'react'
import './css/styles.css'
class IdeaText extends React.Component{
    constructor(props){
        super(props);
        this.state={
            background:null,
            idea:{}
        }
    }
    getGeneratedText(object){
        let idea={
            products:'- ',
            text:''
        }
        for(let key in object){
            if (object[key].value.toLowerCase()==='product'){
                idea.products+=object[key].generated + ' - '
            }else{
                idea.text+=object[key].generated +' '
            }
        }
        if (idea.products === '- '){ idea.products=''}
        this.setState({idea})
    }

    componentDidMount(){
         fetch('https://source.unsplash.com/400x400/?textures').then(
            (response)=>this.setState({background:response.url})
         )
        this.getGeneratedText(this.props.idea)

    }
    renderLine(){
        if (this.state.idea.products!=='') {
            return <hr className='line'/>
        }
    }
    render(){
        if (!this.state.background) {
            return <div> generating... </div>
        }else{
            return(
                
                <div className='idea-text-box modal-content-space'>
                   
                    <img src={this.state.background} alt="loading background..." 
                        className='modal-content-space idea-text-box idea-text-background'
                    />
                    <div className='modal-content-space idea-text-box idea-text-background-cap'/>
                    <p className='idea-text idea-product'>   {this.state.idea.products}</p>
                    {this.renderLine()}
                    <p className='idea-text'>{this.state.idea.text}</p>
                </div>
            )
        }    
        
            
        
    }
}
export default IdeaText 



