import React from 'react'

const style={
    position:'fixed',
    top:'0',
    bottom:'0',
    left:'0',
    right:'0',
    background:'black',
    opacity:'0.7',
    zIndex:'90',
    textAlign:'center'
}
class Modal extends React.Component{
    render(){
        if (this.props.display) {
            return(
                <div style={style}
                    onClick={this.props.onClick}
                >
                    {this.props.children}
                </div>
            )           
        }else{
            return null
        }
    }

}
export default Modal



