import React from 'react';
import {Navbar,NavbarBrand} from 'reactstrap';
const style={
    boxShadow: '0px 3px 3px lightgray'
}
const MuusaNavbar= ()=>{
    return(
        <Navbar light style={style}>
            <NavbarBrand href='/'>
                <img src={require('./../images/isologo.svg')} alt="Muusa" height='25'/>
            </NavbarBrand>
        </Navbar>
    )
};
export default MuusaNavbar



