import React from 'react'
import {Row,Button} from 'reactstrap'
import './css/styles.css'
class LandingPage extends React.Component{
        
    render(){
        return(
            <div className='container'>
                <Row>
                    <div className='col-sm-12 col-md-6'>
                        <h1 className='description-title'>Concept Generator</h1>
                        <div className='description'>
                            <span>Easy generate awesom concept ideas and have fun
                                making cool stuff.</span>  
                        </div>
                        <div>
                            <Button href={ 'webtest/app'} color='primary' className='description-go-button'>
                                    Go get fun!
                            </Button>  
                        </div>
                    </div>
                    <div className='col-sm-12 col-md-6'>
                        <img className='description-image' src={require('./../images/illuDude.png')} alt='yes dude!'/>
                    </div>
                </Row>
            </div>
        );
    }
}
export default LandingPage



