import React from 'react';
import './css/styles.css'
const InputList= ({inputs})=>{
    return(
        inputs.map(item=>{
            return(
                    <React.Fragment key={item.id} >
                        {item.component}
                    </React.Fragment>
           )   
        })
    );
}
export default InputList



