import React from 'react';
import {Button,Row,} from 'reactstrap';
import Input from './../components/input';
import InputList from './../components/input-list';
import WordGenerator from './../services/wordGenerator';
import IdeaCard from './../components/idea-card';
class MainPage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            inputs:[],
            words:{},
            generate:false,
            ideaBackground:''
        };
        //Events-------------------------------
        this.getValue=this.getValue.bind(this);
        this.onAddWord=this.onAddWord.bind(this);
        this.onRemoveAll=this.onRemoveAll.bind(this);
        this.onGenerate=this.onGenerate.bind(this);
        this.onIsClose=this.onIsClose.bind(this);
        this.onInitState=this.onInitState.bind(this);
        this.onIsLock=this.onIsLock.bind(this);
        this.onDisabled=this.onDisabled.bind(this);
        this.onClose=this.onClose.bind(this);
        this.handleModalClose=this.handleModalClose.bind(this);
        this.handleLockPlaceholder=this.handleLockPlaceholder.bind(this);
        //global variables----------------------
        this.component= ( name,initState ) => {return{
            component: (
                <Input name={name} getValue={this.getValue} isClose={this.onIsClose}
                    initState={this.onInitState} isLock={this.onIsLock} disabled={this.onDisabled} 
                    onClose={this.onClose} placeholder={'random'}
                />
            ),
            id: name
        }};
        this.index=0;
    }
    onInitState(initState,name){
        const temporal=this.state.words
        temporal[name]=initState
        this.setState({temporal})
    }
    onIsLock(isLock,name){
        let temporal=this.state.words
        temporal[name].isLock=isLock
        this.setState({temporal})
    }
    handleLockPlaceholder(){
        return 'sisas'
    }
    onDisabled(name){
        const temporal=this.state.words
        if(temporal[name]){
            if (temporal[name].isLock) {
                return true
            }
            else {
                return false
            }
        }
    }
    onClose(name){
        if (this.state.words[name]) {
            return this.state.words[name].isClose
        }
    }    
    onIsClose(isClose, name){
        if (this.state.words[name].isLock) {
            window.alert('This input is in save mode');
            return false
        }else{
            let temporal=this.state.words
            temporal[name].isClose=isClose
            temporal[name].value='';
            this.setState({temporal})
            return true
        }
    }
    getValue(value,name,isLock){
        if ( ! isLock ) {
            let temporal=this.state.words
            temporal[name].value=value===''? 'random' :value;
            this.setState({temporal})
        }
    }
    onAddWord(){
        this.index++;
        this.setState({
            inputs:this.state.inputs.concat( [this.component('word-'+this.index)] )
        })
    }
    onRemoveAll(){
        this.setState({
            inputs:[this.component('word'+this.index)],
            words:{}
        })
    }
    onGenerate(){
        const temporal=this.state.words;
        for (let key in temporal){
            if (!temporal[key].isLock ) {
                temporal[key].generated=WordGenerator(temporal[key].value)
            }
            if(temporal[key].isClose){
                temporal[key].generated=null
            }
        }
        this.setState({words:temporal,generated:true})
    }
    componentDidMount(){
        this.setState({inputs:[this.component('word-0')]})
    }
    handleModalClose(isClose){
        this.setState({generated:!isClose})
    }
    render(){

        return(
            //modal here..out side the container
            <React.Fragment>
                <IdeaCard popUp={this.state.generated}  
                    isClose={this.handleModalClose}
                    words={this.state.words}
                /> 
                <div className='container'>
                    <div className='words-container'>
                        <InputList inputs={this.state.inputs}/>
                    </div>    
                    <Row className='button-container'>
                        <Button className='col remove-all-button' color='primary'
                            onClick={this.onRemoveAll}
                        >
                            Remove all
                        </Button>
                        <Button className='col add-word-button' color='primary'
                            onClick= {this.onAddWord}
                        >
                            Add word
                        </Button>
                    </Row>
                    <Button className='col-12 generate-button' color='primary'
                        onClick={this.onGenerate}
                    >
                        Generate
                    </Button>
                </div>
            </React.Fragment>
        )
    }
}
export default MainPage 



