import React from 'react'

const onClick=(props)=>{
    if (props.isClose) {
        props.isClose(true)
    }
}
const style={
    cursor:'pointer'
}
const CloseWord= (props)=>{
    return(
        <div onClick={()=>onClick(props)} style={style}> 
            <img src={require('./../images/close.svg')} alt="close" className='icon'/>
        </div>
    )
}
export default CloseWord



