import RitaTypes from './../data/ritaTypes'
import Occupation from './../data/occupations'
import Product from './../data/product'
import buildArrayFromObject from './../services/buildArrayFromObject'

const Rita = window.RiTa;
const ritaLists = buildArrayFromObject(RitaTypes,'list');
const ritaOptions=buildArrayFromObject(RitaTypes,'option')
const productList = Product.list;
const occupationList = Occupation.list;

function WordGenerator ( option ){
    switch( option.toLowerCase() ){
        case 'product':
            return productGenerator();
        case 'occupation':
            return occupationGenerator();
        case 'all':
            return randomGenerator();
        default:
            if (option[0]==='*') {
                return option.slice(1)
            }
            let ritaMatch=null
            for (var i = 0; i < ritaLists.length; i++) {
              // console.log(ritaLists[i])
              if (option===ritaOptions[i]) {
                  ritaMatch=i;
              }
          }
          if (ritaMatch!==null) {
              return ( ritaGenerator(ritaLists[ritaMatch]) )
          }else{}
    }
}
function productGenerator(){
    const random=Math.floor( (Math.random()*productList.length) );
    return productList[random];
}
function occupationGenerator(){
    const random=Math.floor( (Math.random()*occupationList.length) );
    return occupationList[random];
}
function ritaGenerator( list ){
    if (list==='all') { 
        return Rita.randomWord()
    }console.log('se supone');
    return Rita.randomWord(list)
}
function randomGenerator(){
    const randomList=[ritaGenerator('all'),productGenerator(),occupationGenerator()]
    const random=Math.floor( (Math.random()*randomList.length) );
    return randomList[random]
}

export default WordGenerator



