import React from 'react';
import Autocomplete from './Autocomplete';
import OPTIONS from './../constants/options';
import {Row } from 'reactstrap';
import LockWord from './lock-word';
import CloseWord from './close-word';
import './css/styles.css';

class Input extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isClose:false,
            isLock:false,
            value:''
        }
        this.onIsClose=this.onIsClose.bind(this);
        this.onIsLock=this.onIsLock.bind(this);
    }
    onIsLock(isLock){
        this.setState({isLock})
        if (this.props.isLock) {
            this.props.isLock(isLock,this.props.name)
        }
    }
    onIsClose(isClose){
        this.setState({isClose})
        this.props.isClose(isClose,this.props.name);
    }
    componentDidMount(){
        const initState={
            value:'random',
            isClose:false,
            isLock:false,
            generated:''
        }
        if(this.props.initState){
            this.props.initState(initState,this.props.name)
        }
    }
    render(){
        if (! this.props.onClose(this.props.name)) {
            return(
                <Row>
                    <div className='lock-word-type'>
                        <LockWord isLock={this.onIsLock}/>
                    </div>
                    <div className=' word-type'>
                        <Autocomplete 
                            dataSource={OPTIONS}
                            getValue={(value,name)=>{ this.props.getValue(value,name,this.state.isLock)}}
                            name={this.props.name}
                            max={5}
                            disabled={this.props.disabled(this.props.name)}
                            initValue={''}
                            placeholder={this.props.placeholder}
                        />   
                    </div>
                    <div className='delete-word-type'>
                        <CloseWord isClose={this.onIsClose}/>
                    </div>
                </Row>
            );
        }else{
            return null
        }
    }
}
export default Input



