import React from 'react'
class  LockWord extends React.Component{
    constructor(props){
        super(props);
        this.state={
            lock:false
        };
        this.style={
            cursor:'pointer',

        };
        this.onLock=this.onLock.bind(this);

    }
    onLock(event){
        this.setState({lock:!this.state.lock})
    }
    componentDidUpdate(prevProps,prevState){
        if (prevState !== this.state) {
            
            
                this.props.isLock(this.state.lock)
            
        }
    }
    render(){
                if (this.state.lock) {
            return(
                <div style={this.style}onClick={this.onLock}>
                    <img src={require("./../images/lock.svg")} alt="lock" className='icon' />
                </div>
            )
        }else{
            return(
                <div style={this.style}onClick={this.onLock}> 
                    <img src={require('./../images/unlock.svg')} alt="unlock" className='icon'/>
                </div>
            )
        }        
    }
}
export default LockWord



