import React from 'react'
import Modal from './modal'
import './css/styles.css'
import IdeaText from './idea-text'
class IdeaCard extends React.Component{
    constructor(props){
        super(props);
        this.state={
            canClose:false,
            isClose:false
        }
        this.handleMouseOut=this.handleMouseOut.bind(this);
        this.handleMouseOver=this.handleMouseOver.bind(this);
        this.handleModalClick=this.handleModalClick.bind(this);
    }
    handleMouseOut(){
        this.setState({canClose:true})
    }
    handleMouseOver(){
        this.setState({canClose:false})
    }
    handleModalClick(){
        if (this.state.canClose) {
            this.setState({isClose:true})
            this.props.isClose(true)
        }
    }
    render(){
        if (this.props.popUp) {
            
            return(
                <React.Fragment>
                    <Modal display
                    />
                        
                <div style={{position:'absolute',right:'2vw',zIndex:'92',color:'white',cursor:'pointer'}} onClick={()=>{this.setState({isClose:true});this.props.isClose(true)}}>X</div>
                    <div style={{textAlign:'center',zIndex:'91',position:'absolute',top:'0',bottom:'0',left:'0',right:'0'}}
                        onClick={this.handleModalClick}
                    >
                        <div className='modal-content-space'
                            onMouseOut={this.handleMouseOut}
                            onMouseOver={this.handleMouseOver}
                        >
                            <IdeaText idea={this.props.words}/>
                        </div>
                    </div>
                </React.Fragment>

            );
        }else{return null}
    }
    
}
export default IdeaCard 



